import { Box } from "@chakra-ui/react";
import '../Inspirations.css';

const BathroomTiles = () => {
    return (
        <Box maxWidth="8xl" margin="30px 60px" display="flex" flexDirection="column" gap="1.2rem">
            <h3 style={{ fontSize: "22px" }}><strong>5 Things to Consider When Choosing Bathroom Tiles for Malaysian Homes</strong></h3>
            <hr></hr>
            <img src="https://lorenzaceramica.com/wp-content/uploads/2020/11/lorenza-ceramica-bathroom-tiles-malaysian-home-ceramic-porcelain.jpg" alt="post-banner"></img>
            <p>Bathroom remodelling can involve a lot of work including plumbing, putting fixtures, lighting, and so forth. It can get really expensive, so picking the right materials can save you a lot of money in the long run.</p>
            <p>In this article, we will be focusing on flooring tiles, the part that you will pay the most attention to regardless of your bathroom renovation budget.&nbsp;</p>
            <p>How do you pick the right bathroom tiles? Here are a few factors you should consider…</p>
            <h2 className="has-text-color">1. Durability</h2>
            <p>When it comes to cleaning, we do love our heavy chemicals on practically everything. Sambal stains on a white shirt? Bleach. Fear of germs in your bathroom and kitchen? Bleach. In fact, it’s hard to find a Malaysian household without at least a bottle of Clorox under their sink cabinet.</p>
            <p>And no other surface in the house are more subjected to these heavy-duty usage, than the bathroom floors. Not only they have to withstand these regular splashing and cleaning products, but also hard scrubbing to get rid of the nastiest dirt.</p>
            <p>Ceramic and porcelain are the most common choices to protect you against wear and scratches.</p>
            <p>You also need to take into account how waterproof the tiles are, especially for high-splash zones. Plastic laminate or wood, for example, can absorb moisture thus they are more suitable for powder rooms as opposed to wet rooms.&nbsp;</p>
            <h2 className="has-text-color">2. Maintenance</h2>
            <p>Naturally, highly durable tiles are also longer-lasting, as they don’t change much in shape or colour over the years.&nbsp;</p>
            <p>However, a certain type of tiles might require more maintenance than the others. Stone floors, for one, need more cleaning and sealing as compared to ceramic tiles. </p>
            <p>Also, consider the grout that you will be using. If the sight of dirty grouts makes you start twitching, opt for dark-coloured ones. If you can’t see it, it doesn’t exist, right? (Just kidding! Make sure you take care of its cleanliness just like any other parts of the bathroom).</p>
            <h2 className="has-text-color">3. Safety</h2>
            <p>It’s typical for Malaysians households to not only have children but also elderly folks living together, which is why floor safety is paramount. You want to make sure the surface is easy on the feet and doesn’t get slippery when wet.</p>
            <p>Other than the material itself, tile sizes can also influence how much traction you’ll get from the floor. Small tiles are easier to grip on since there are more grout joints. Big tiles, on the other hand (or foot), are more slippery, unless you go for the textured ones.</p>
            <p>Additionally, you can also get anti-slip bathroom mats for added security over your loved ones.</p>
            <h2 className="has-text-color">4. Ease of installation</h2>
            <p>Either you are doing a DIY project or hiring someone for your renovation, ease of installation certainly does contribute as a cost factor.</p>
            <p>Common tiles such as ceramic and porcelains can be fairly easy to put on; while fancier ones such as glass or stone tiles might require assistance from the experts.</p>
            <h2 className="has-text-color">5. Appearance</h2>
            <p>Function or form… why not both?</p>
            <p>As bathroom designs keep innovating, the industry has come up with all kind of materials that are both practical and aesthetically pleasing at the same time.</p>
            <p>These days, there are porcelain tiles that capture that wooden aesthetic but without the drawback of possibly getting stained by moisture.&nbsp; </p>
            <p>You can also play around with the tile sizes to achieve the desired feel. Avoid using big tiles in a small bathroom as it can make the room look even smaller</p>
            <h2 className="has-text-color"><a href="/products//room/bathroom">Ready to renovate your bathroom? Click here to check out the best price for bathroom tiles in Klang Valley</a></h2>
        </Box>
    )
}

export default BathroomTiles;